import React, {Component} from 'react';
import {api, reducer} from '../api';
import WordGraph from "./WordGraph";

import './App.css';
import * as PropTypes from "prop-types";

class App extends Component {
    state = {
        palavraBusca: "",
        currentNode: null,
        elements: {
            nodes: [],
            edges: [],
            clusters: [], // para o cise layout
        },
        significados: []
    };

    componentDidMount() {
        document.addEventListener("click", () => this.clearSignificados())
    }

    onChangeBusca = e => {
        this.setState({palavraBusca: e.target.value})
    };

    onSearch = e => {
        e.stopPropagation();
        e.preventDefault();

        const {palavraBusca} = this.state;

        this.fetchDominios(palavraBusca, 1);
    };


    onClickNode = node => {
        const currentPage = node.data("page") || 0;
        const nextPage = currentPage + 1;

        const isPalavra = node.data('type') === 'palavra';
        isPalavra ? this.onClickPalavra(node, nextPage) : this.onClickDominio(node, nextPage)
    };

    onClickPalavra = (node, page) => {
        const palavra = node.data("label");
        this.fetchDominios(palavra, page);
    };

    onClickDominio = (node, page) => {
        const dominio = node.data("label");
        const palavra = node.data("context");
        this.fetchRelacionadas(dominio, palavra, page);
    };

    onClickEdge = edge => {
        const dominio = edge.data("dominio");
        const palavra = edge.data("palavra");
        this.fetchSignificados(dominio, palavra);
    };

    fetchDominios = (palavra, page) => {
        api.dominios(palavra, page)
            .then(({data}) => reducer.receiveDominios(this.state, {
                palavra,
                page,
                data
            }))
            .then(newState => this.setState(newState));
    };

    fetchRelacionadas = (dominio, palavra, page) => {
        api.relacionadas(dominio, palavra, page)
            .then(({data}) => reducer.receiveRelacionadas(this.state, {
                palavra,
                dominio,
                page,
                data
            }))
            .then(newState => this.setState(newState));
    };

    fetchSignificados = (dominio, palavra) => {
        api.significados(dominio, palavra)
            .then(({data}) => reducer.receiveSignificados(this.state, {
                data
            }))
            .then(newState => this.setState(newState));
    };

    clearSignificados = () => this.setState({significados: []});

    render() {
        const {palavraBusca, currentNode, elements, significados} = this.state;

        return (
            <div className="App">
                <Header buscaValue={palavraBusca}
                        onChangeBusca={this.onChangeBusca}
                        onSearch={this.onSearch}/>

                <div className="content">
                    {significados.length > 0 &&
                    <Significados value={significados}/>}

                    <WordGraph context={currentNode}
                               elements={elements}
                               onClickNode={this.onClickNode}
                               onClickEdge={this.onClickEdge}
                    />
                </div>
            </div>
        );
    }
}

const Header = ({buscaValue, onChangeBusca, onSearch}) => {
    return (
        <div className="Header">
            <span className="title">Lácio</span>

            <form onSubmit={onSearch}>
                <input type="text"
                       value={buscaValue}
                       autoFocus
                       onChange={onChangeBusca}
                />
                <input type="submit" value="Buscar"/>
            </form>
        </div>
    )
};

class Significados extends Component {

    constructor(props) {
        super(props);
        this.ref = React.createRef();
    }

    componentDidMount() {
        this.ref.current.addEventListener('click', e => e.stopPropagation());
    }

    render() {
        let {value} = this.props;

        return (
            <div className="Significados" ref={this.ref}>
                <div className="title">Significados</div>

                {value.map((it, i) =>
                    <div className="significado" key={i}>
                        <span className="index">{i + 1}.</span>

                        {it.dominios.length > 0 &&
                        <span className="dominios">{it.dominios.join(", ")}</span>}

                        <span className="texto">{it.texto}</span>
                        {it.score > 0 &&

                        <span className="score">{it.score}</span>}
                    </div>
                )}
            </div>
        )
    }
}

Significados.propTypes = {value: PropTypes.any}


export default App;
